import { mapGetters } from 'vuex';

export default {
  name: 'PinView',
  props: {
    pinView: {
      type: Object,
      default: null,
    },
  },
  computed: {
    ...mapGetters({
      categories: 'categories/loadCategories',
    }),
    getColor() {
      const target = this.categories.find((category) => category.id === this.pinView.categoryId);
      if(target) {
        return target.color
      }
      return "#000";

    },
  }
};
