import { mapGetters } from 'vuex';

import iconBase from './iconBase.vue';

export default {
  name: 'MyFilter',
  components: {
    iconBase,
  },
  emits: ['call-filter'],
  data() {
    return {
      active: false,
    };
  },
  computed: {
    ...mapGetters({
      categories: 'categories/loadCategories',
      colors: 'pins/getColors',
    }),
  },
};
